import './App.css';
import HomePage from './components/pages/HomePage';
import { Grid } from "@material-ui/core";
import io from "socket.io-client";
import { useState } from 'react';
import ErrorPage from './components/pages/ErrorPage';
import RefusedPage from './components/pages/RefusedPage';

function App() {
    const socket = io();
    const [page, setPage] = useState("home");
    const [admin, setAdmin] = useState(false);
    const [players, setPlayers] = useState(0);
    socket.on("refused", () => {
        setPage("refused");
    });
    socket.on("admin", () => {
        setAdmin(true);
    })
    socket.on("join", () => {
        setPlayers(players + 1);
    })
    socket.on("leave", () => {
        setPlayers(players - 1);
    })
    return (
        <Grid container
            spacing={0}
            direction="column"
            alignItems="center"
            justify="center" style={{ textAlign: "center", minHeight: '100vh' }}>
            <Grid>
                {
                    page == "home" ? <HomePage socket={socket} admin={admin} players={players} /> : page == "refused" ? <RefusedPage /> : <ErrorPage />
                }
                <div class="copy">
                    <p>Quiz Battle Royal &copy; 2021 Ruben Kober</p>
                </div>
            </Grid>
        </Grid>
    );
}

export default App;
